/* #Reset default styles - start
   ========================================================================== */

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font,
ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
    background: transparent;
    border: 0;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
    outline: none;
}

article, aside, details, figcaption, figure, footer, header, nav, section {
    display: block;
}

del {
    text-decoration: line-through;
}

/* tables still need 'cellspacing="0"' in the markup */
table {
    border-collapse: collapse;
    border-spacing: 0;
    border: medium none;
    vertical-align: middle;
}

table th {
    border: 0;
    padding: 5px 7px;
    text-align: center;
}

table td {
    padding: 5px 10px;
    text-align: center;
}

a img {
    border: none;
}

img, .wp-caption {
    max-width: 100%;
    height: auto;
}

iframe {
    max-width: 100%;
}

/* #Reset default styles - end
   ========================================================================== */

/* #General styles that are used in our theme - start
 * Here are defined global styles on body element, on container elements that hold every page,
 * headings and links, inputs, core wp styles and default pagination styles
   ========================================================================== */

.clearfix:before,
.clearfix:after {
    content: " ";
    display: table;
}

.clearfix:after {
    clear: both;
}

.clearfix {
    *zoom: 1;
}

.clear:after {
    clear: both;
    content: "";
    display: block;
}

html {
    overflow-y: scroll !important; /* for vertical split screen slider shortcode on touch devices */
}

body {
    font-family: $default-text-font;
    font-size: 15px;
    line-height: 25px;
	font-weight: 300;
    color: $default-text-color;
    background-color: $default-background-color;
    overflow-x: hidden !important;
    -webkit-font-smoothing: antialiased;
}

h1 {
	font-size: 50px;
	line-height: 1.1em;
	font-weight: 500;
}

h2 {
	font-size: 40px;
	line-height: 1.25em;
	font-weight: 500;
}

h3 {
	font-size: 30px;
	line-height: 1.333333333333333em;
	font-weight: 500;
}

h4 {
	font-size: 28px;
	line-height: 1.178571428571429em;
	font-weight: 400;
}

h5 {
	font-size: 20px;
	line-height: 1.5em;
	font-weight: 500;
}

h6 {
	font-size: 15px;
	line-height: 1.666666666666667em;
	font-weight: 700;
}

h1, h2, h3, h5, h6 {
	color: $default-heading-color;
	font-family: $default-heading-font;
}

h4 {
	color: $additional-text-color;
	font-family: $additional-text-font;
}

h1, h2, h3, h4, h5, h6 {
    margin: 25px 0;
	letter-spacing: -.01em;
    
    a {
        @include eltdfTypographyLayout();
        
        &:hover {
            color: $first-main-color;
        }
    }
}

a,
p a {
    color: $default-heading-color;
    text-decoration: none;
    @include eltdfTransition(color .2s ease-out);

    &:hover {
        color: $first-main-color;
    }
}

p {
    margin: 10px 0;
}

input[type='submit'] {
    -webkit-appearance: none;
}

ul, ol {
    list-style-position: inside;
    margin: 15px 0;
}

ul ul,
ol ol,
ol ul,
ul ol {
    margin: 0;
    padding: 0 0 0 15px;
}

pre {
    background-color: $default-background-color;
    padding: 15px;
    max-width: 100%;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin: 15px 0;
}

dt {
    font-size: 15px;
    font-weight: bold;
    margin-bottom: 10px;
}

dd {
    margin-bottom: 15px;
}

sub, sup {
    font-size: 75%;
    height: 0;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}

sub {
    top: 0.5ex;
}

sup {
    bottom: 0.5ex;
}

.wp-caption-text {
    margin: 0;
    font-style: italic;
}

table {
    width: 100%;
}

table thead tr, table tbody tr {
    border: 1px solid $default-border-color;
}

table tbody th {
    border-right: 1px solid $default-border-color;
}

blockquote {
    @include eltdfRelativeHolderLayout();
    margin: 10px 0;
    padding: 0;
    font-family: $default-heading-font;
    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    text-align: left;
    quotes: none;
    box-sizing: border-box;

    > * {
        @include eltdfTypographyLayout();
        margin: 0;
	    color: #363636;
    }

    &:before {
        content: '\f101';
        font-family: fontAwesome;
        display: inline-block;
        float: left;
        font-size: 25px;
        padding: 0 5px;
        color: $first-main-color;
    }
    &:after {
        content: '';
    }

	cite {
		font-style: normal;
		font-weight: 400;
	}
}

/* Webkit */
::selection {
    background: $first-main-color;
    color: $default-background-color;
}

/* Gecko/Mozilla */
::-moz-selection {
    background: $first-main-color;
    color: $default-background-color;
}

.wp-caption,
.wp-caption-text,
.sticky,
.gallery-caption,
.bypostauthor {
    opacity: 1;
}

.alignleft {
    display: inline;
    float: left;
    margin: 0 20px 20px 0;
}

.alignright {
    display: inline;
    float: right;
    margin: 0 0 20px 20px;
}

.aligncenter {
    clear: both;
    display: block;
    margin: 10px auto;
}


.alignwide {
    margin-left: auto;
    margin-right: auto;
    clear: both;
}

@include ipad-landscape() {
    .alignwide {
        width: 100%;
        max-width: 100%;
    }
}

.alignfull {
    position: relative;
    left: -1rem;
    width: calc(100% + (2 * 1rem));
    max-width: calc(100% + (2 * 1rem));
    clear: both;
}

@include ipad-landscape() {
    .alignfull {
        left: calc(-12.5% - 75px);
        width: calc(125% + 150px);
        max-width: calc(125% + 150px);
        margin-top: calc(2 * 1rem);
        margin-bottom: calc(2 * 1rem);
    }
}

.text-align-right {
    text-align: right;
}

.text-align-left {
    text-align: left;
}

.text-align-center {
    text-align: center;
}

.eltdf-side-menu {
    
    .eltdf-iwt-title-text {
        font-size: 12px;
        padding: 6px 0 0 11px;
    }
}

.eltdf-wrapper {
    position: relative;
    left: 0;
    z-index: 1000;
    @include eltdfTransition(left 0.33s cubic-bezier(0.694, 0.0482, 0.335, 1));

    .eltdf-wrapper-inner {
        width: 100%;
        overflow: hidden;
    }
}

.eltdf-content {
    position: relative;
    margin-top: 0;
    z-index: 100;
    background-color: $default-background-color; /*this is needed because of the uncovering footer*/
}

.eltdf-boxed {
    overflow: hidden !important; /* this is because content jumping while using accordion and display:inline-block on 'body.eltdf-boxed .eltdf-wrapper'   */

    .eltdf-wrapper {
        display: inline-block;
        vertical-align: middle;
        width: 100%;
    
        .touch & {
            background-repeat: repeat;
        }
        
        .eltdf-wrapper-inner {
            width: $grid-width-boxed;
            overflow: visible;
            margin: auto;
        }
    }

    .eltdf-content {
        overflow: hidden;
    }

    .eltdf-footer-inner {
        width: $grid-width-boxed;
        margin: auto;
    }

    .eltdf-page-header .eltdf-sticky-header,
    .eltdf-page-header .eltdf-fixed-wrapper.fixed {
        width: $grid-width-boxed;
        left: auto;
    }
}

.eltdf-paspartu-enabled {

    .eltdf-wrapper {
        padding: 10px;
        background-color: #fff;
        box-sizing: border-box;
    }

    &.eltdf-top-paspartu-disabled {

        .eltdf-wrapper {
            padding-top: 0 !important;
        }
    }
	
	.eltdf-page-header {
		
		.eltdf-fixed-wrapper {
			
			&.fixed {
				left: 10px;
				width: calc(100% - 20px);
			}
		}
	}
	
	.eltdf-sticky-header {
		left: 10px;
		width: calc(100% - 20px);
	}
	
	.eltdf-mobile-header {
		
		&.mobile-header-appear {
			
			.eltdf-mobile-header-inner {
				left: 10px;
				width: calc(100% - 20px);
			}
		}
	}
    
    &.eltdf-fixed-paspartu-enabled {
        
        &.admin-bar {
    
            .eltdf-wrapper {
                
                &:before {
                    top: 32px;
                }
            }
        }
    
        .eltdf-wrapper {
            
            &:before,
            &:after {
                content: '';
                position: fixed;
                left: 0;
                width: 100%;
                height: auto;
                padding-top: inherit;
                background-color: inherit;
                z-index: 99999;
            }
    
            &:before {
                top: 0;
            }
    
            &:after {
                bottom: 0;
            }
        }
    
        .eltdf-page-header {
            
            .eltdf-fixed-wrapper {
                
                &.fixed {
                    margin-top: 10px;
                }
            }
        }
    
        .eltdf-sticky-header {
        
            &.header-appear {
                margin-top: 10px;
            }
        }
	
	    .eltdf-mobile-header {
		    
		    &.mobile-header-appear {
			    
			    .eltdf-mobile-header-inner {
				    margin-top: 10px;
			    }
		    }
	    }
    }
}

.eltdf-smooth-transition-loader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2000;
    background-color: #fff;
}

.eltdf-container {
    position: relative;
    padding: 0;
    width: 100%;
    z-index: 100;

    &:before {
        content: " ";
        display: table;
    }

    &:after {
        content: " ";
        display: table;
        clear: both;
    }
}

.eltdf-container-inner { //not in parent selector since this class is used for positioning other elements in grid
    width: $grid-width;
    margin: 0 auto;
}

.eltdf-full-width {
    position: relative;
    z-index: 100;
}

.eltdf-content .eltdf-content-inner > .eltdf-container > .eltdf-container-inner,
.eltdf-content .eltdf-content-inner > .eltdf-full-width > .eltdf-full-width-inner {
	padding-top: 42px; // default value for content top padding
    padding-bottom: 40px; // default value for content bottom padding
}

