/* #General styles that are used in our theme - end
   ========================================================================== */

@include keyframes(preload-background) {
    from {
        background-position: 0 0;
    }
    to {
        background-position: 100% 0;
    }
}

.eltdf-preload-background {
    background: transparent url('../img/preload_pattern.png') repeat scroll inherit !important;
    @include animation(preload-background, 40s, infinite, linear, 0s);
}

.eltdf-grid {
    width: $grid-width;
    margin: 0 auto;
}

.eltdf-vertical-align-containers {
    position: relative;
    height: 100%;
    width: 100%;
    padding: 0 20px;
    box-sizing: border-box;

    &.eltdf-25-50-25 {

        .eltdf-position-left,
        .eltdf-position-right {
            width: 25%;
        }

        .eltdf-position-center {
            width: 50%;
            left: 50%;
            @include eltdfTransform(translateX(-50%));
        }
    }

    &.eltdf-33-33-33 {

        .eltdf-position-left,
        .eltdf-position-center,
        .eltdf-position-right {
            width: 33.33%;
        }

        .eltdf-position-center {
            width: 50%;
            left: 50%;
            @include eltdfTransform(translateX(-50%));
        }
    }

    &.eltdf-50-50 {

        .eltdf-position-left,
        .eltdf-position-right {
            width: 50%;
        }
    }

    &.eltdf-33-66 {
        
        .eltdf-position-left {
            width: 33.33%;
        }

        .eltdf-position-right {
            width: 66.66%;
        }
    }

    &.eltdf-66-33 {
        
        .eltdf-position-left {
            width: 66.66%;
        }

        .eltdf-position-right {
            width: 33.33%;
        }
    }

    .eltdf-position-left {
        position: relative;
        float: left;
        z-index: 2;
        height: 100%;
    }
    
    .eltdf-position-center {
        height: 100%;
        width: 100%;
        position: absolute;
        left: 0;
        text-align: center;
        z-index: 1;
    }

    .eltdf-position-right {
        position: relative;
        float: right;
        z-index: 2;
        height: 100%;
        text-align: right;
    }

    .eltdf-position-left-inner,
    .eltdf-position-center-inner,
    .eltdf-position-right-inner {
        vertical-align: middle;
        display: inline-block;
    }
    
    .eltdf-position-right.eltdf-has-widget .eltdf-position-right-inner {
        height: 100%;
    }

    .eltdf-position-left:before,
    .eltdf-position-center:before,
    .eltdf-position-right:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        height: 100%;
        margin-right: 0; /* adjusts for spacing before it was -3px */
    }
}

/* ==========================================================================
