.eltdf-parallax-row-holder {
	position: static;
	padding: 0;
	background-repeat: no-repeat;
	background-color: transparent;
	background-position: center 0;
	background-attachment: fixed;
	overflow: hidden;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    transform: translateZ(0);
    
    &.eltdf-parallax-with-overlay {
    
        @media only screen and (min-width: $ipad-landscape-plus-pixel) {
            position: relative;
        }
    }
	
	@include ipad-landscape {
		height: auto !important;
		min-height: 200px !important;
		background-position: center top !important;
		background-attachment: scroll;
		background-size: cover;
	}
}
/* ==========================================================================
   #Row styles - end
   ========================================================================== */

   /* #Cut-off effect styles - start
   ========================================================================== */

.eltdf-cut-off-effect {
  position: absolute;
  z-index: 20;
  display:block;

  &.eltdf-cut-off-effect-top {
    top: -26px;
  }

  &.eltdf-cut-off-effect-bottom {
    bottom: -26px;
    @include eltdfTransform(rotate(180deg));
  }

  path{
    fill: #fff;
  }
}
/* #Cut-off effect styles - end
  ========================================================================== */

/* ==========================================================================
   #VC Revolution Slider styles - begin
   ========================================================================== */

.eltdf-rev-has-paspartu {
	@include eltdfRelativeHolderLayout();
    height: 100vh;
    margin: 0;
    box-sizing: border-box;
    
    .admin-bar & {
        height: calc(100vh - 32px);
    }
	
	&.eltdf-side-paspartu-disabled {
		padding-left: 0 !important;
		padding-right: 0 !important;
	}
	
	&.eltdf-paspartu-tiny {
		padding: 10px;
		
		&.eltdf-top-paspartu-disabled {
			padding-top: 0;
			
			.forcefullwidth_wrapper_tp_banner {
				
				.tp-fullwidth-forcer {
					height: calc(100vh - 10px) !important;
					
					.admin-bar & {
						height: calc(100vh - 42px) !important;
					}
				}
			}
		}
		
		.forcefullwidth_wrapper_tp_banner {
			
			.tp-fullwidth-forcer {
				height: calc(100vh - 20px) !important;
				
				.admin-bar & {
					height: calc(100vh - 52px) !important;
				}
			}
		}
	}
	
	&.eltdf-paspartu-small {
		padding: 20px;
		
		&.eltdf-top-paspartu-disabled {
			padding-top: 0;
			
			.forcefullwidth_wrapper_tp_banner {
				
				.tp-fullwidth-forcer {
					height: calc(100vh - 20px) !important;
					
					.admin-bar & {
						height: calc(100vh - 52px) !important;
					}
				}
			}
		}
		
		.forcefullwidth_wrapper_tp_banner {
			
			.tp-fullwidth-forcer {
				height: calc(100vh - 40px) !important;
				
