/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
	}
}

.wpb_widgetised_column {
    position: relative;

    .widget {
	
	    &:last-child {
		    margin: 0 !important;
	    }
    }
}
/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
	margin: 0 0 35px;
	
	.eltdf-widget-title {
		margin: 0 0 17px;
		
		a {
			color: inherit;
		}
	}
	
	ul {
		padding: 0;
		margin: 0;
		list-style: none;
		
		li {
			margin: 0 0 10px;
			
			&:last-child {
				margin: 0;
			}
		}
	}

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
	        font-weight: 500;
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.widget_rss {

        .eltdf-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;
    
                .rsswidget {
                    font-weight: 500;
                }

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.widget_search {

        form {
	        @include eltdfRelativeHolderLayout();
	        clear: both;
	
	        > div {
		        @include eltdfRelativeHolderLayout();
		        border: 2px dotted $first-main-color;
		        border-radius: 5px;
		        box-sizing: border-box;
	        }
        }

        input,
        button {
	        position: relative;
	        display: inline-block;
	        vertical-align: top;
	        float: left;
	        margin: 0;
	        height: 54px;
	        line-height: 32px;
	        color: $default-heading-color;
	        background-color: transparent;
	        border: 0;
	        border-radius: 0;
	        outline: 0;
	        -webkit-appearance: none;
	        box-sizing: border-box;
	        @include eltdfTransition(color .2s ease-out);
        }

        input {
	        width: calc(100% - 40px);
	        padding: 10px 10px 10px 20px;
	        font-family: $default-heading-font;
	        font-size: 16px;
	        font-weight: 500;
	
	        @include eltdfPlaceholder {
		        color: inherit;
	        }
        }

        button {
	        width: 40px;
	        padding: 10px 13px 10px 4px;
	        font-size: 15px;
	        cursor: pointer;
	
	        &:hover {
		        color: $first-main-color;
	        }
	
	        span {
		        display: block;
		        line-height: inherit;
		
		        &:before {
			        display: block;
			        line-height: inherit;
		        }
	        }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
	            margin: 2px 0;
	
	            a {
		            color: $default-text-color;
		
		            &:hover {
			            color: $default-heading-color;
		            }
	            }

                ul {
                    padding: 0 0 0 10px;
                }
            }
        }
    }
	
	&.widget_archive {
		
		.eltdf-widget-title {
			margin-bottom: 9px;
		}
		
		select {
			margin-top: 10px;
		}
	}
	
	&.widget_calendar {
		
		.eltdf-widget-title {
			margin-bottom: 12px;
		}
	}
	
	&.widget_categories {
		
		.eltdf-widget-title {
			margin-bottom: 9px;
		}
		
		form {
			margin-top: 19px;
		}
	}
	
	&.widget_meta,
	&.widget_recent_comments,
	&.widget_nav_menu,
	&.widget_rss,
	&.widget_pages {
		
		.eltdf-widget-title {
			margin-bottom: 9px;
		}
	}

    &.widget_recent_entries {
	
	    .eltdf-widget-title {
            margin: 0 0 9px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
	                font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
	        max-width: 100%;
	        height: 54px;
	        padding: 0 18px;
	        line-height: 54px;
	        color: $default-heading-color;
	        background-color: transparent;
	        border: 2px dotted $first-main-color;
	        border-radius: 5px;
	        font-family: $default-heading-font;
	        font-size: inherit;
	        font-style: inherit;
	        font-weight: 500;
	        outline: none;
	        box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {
            
            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_tag_cloud {

        .eltdf-widget-title {
            margin: 0 0 7px;
        }

        a {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-family: inherit;
            font-size: 15px !important;
            text-transform: capitalize;
            background-color: transparent;
            color: $default-text-color;
    
            &:hover {
                color: $default-heading-color;
            }
    
            &:after {
                content: ',';
                color: $default-text-color;
            }
    
            &:last-child {
        
                &:after {
                    display: none;
                }
            }
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.eltdf-page-header,
.eltdf-top-bar,
.eltdf-sticky-header,
.eltdf-mobile-header {
    
    .widget {
	    display: inline-block;
	    vertical-align: middle;
        margin: 0;
        
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.eltdf-page-footer {
	
    .widget {
	    color: #fff;
	
	    &:last-child {
		    margin: 0 !important;
	    }
	    
        h1, h2, h3, h4, h5, h6 {
            margin: 0;
	        color: #fff;
        }
	    
	    .eltdf-widget-title {
		    color: #fff;
		    margin: 0 0 21px;
	    }
	
	    a {
		    color: #fff;
		
		    &:hover {
			    color: $first-main-color;
		    }
	    }
	    
        table thead tr,
        table tbody tr {
            border-color: #fff;
        }

        table tbody th {
            border-right-color: #fff;
        }

        /*
        *   Specific styles
        */
	    #wp-calendar {
		
		    caption {
			    color: #fff;
		    }
	    }
	
	    &.widget_rss {
		
		    .eltdf-footer-widget-title {
			
			    .rsswidget {
				    color: #fff;
				
				    &:hover {
					    color: $first-main-color;
				    }
			    }
		    }
	    }
	    
        &.widget_search {

            form {
	            margin-top: 10px;

                > div {
                    border-color: $first-main-color;
                }
            }

            input {
                color: #fff;
	            
	            @include eltdfPlaceholder {
		            color: inherit;
	            }

                &[type='submit'] {
                    color: #fff;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #fff;
                border-color: $first-main-color;

                option {
                    background-color: #363636;
                }
            }
	        
	        a {
		        color: #fff;
		        
		        &:hover {
			        color: $first-main-color;
		        }
	        }
        }

        &.widget_tag_cloud,
	    &.widget_recent_comments,
	    &.widget_pages,
	    &.widget_meta,
        &.widget_nav_menu {

            a {
                color: #fff;

                &:after {
                    color: #fff;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
	
	.widget_eltdf_instagram_widget {
		.eltdf-widget-title {
			margin: 0 0 24px;
		}
	}
}

.eltdf-top-bar {

	a {
		color: #969696;

		&:hover {
			color: $first-main-color;
		}
	}

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}

.widget {
	
    &.eltdf-opening-hours-widget {
        margin: 0 0 14px;
    }
} 
/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */