@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
	    border-top: 1px solid rgba($default-heading-color, .1);
	    padding: 30px 0 10px;
    }
	
	.eltdf-comment {
		padding-bottom: 38px;
		border-bottom: 1px solid rgba($default-heading-color, .1);
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
	        margin: 14px 0 0;
	        padding: 13px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 25px;
	    
        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 10px;
        left: 0;
        width: 82px;
        height: 82px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
	        margin: 6px 0 10px;
        }
	
	    .comment-edit-link,
	    .comment-reply-link {
		    color: $default-heading-color;
		    font-weight: 500;
		    font-size: 15px;
		    line-height: 18px;
		    text-transform: lowercase;
	    }
	    
	    .comment-reply-link {
		    float: right;
	    }
	    
        .replay,
        .comment-reply-link,
        .comment-edit-link {
            margin-left: 0px;
	        font-family: $default-heading-font;

            &:hover {
                color: $default-text-color;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
	        font-size: 16px;
	        font-weight: 500;
	        
	        &:after {
		        content: '/';
		        padding: 0 5px;
		        margin-left: -3px;
		        font-weight: 300;
	        }
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
	        font-family: $default-heading-font;
            font-size: 12px;
	        font-weight: 400;
        }

        .eltdf-text-holder {
	        padding-top: 2px;
	        padding-bottom: 17px;
	        p {
		        margin: 7px 0;
	        }
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
	        border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
	
	.eltdf-comment-form-inner {
		padding: 22px 0 78px;
		
		.comment-form {
			label {
				font-family: $default-heading-font;
			}
		}
		
		.form-submit {
			text-align: right;
		}
		
		.eltdf-comment-form-label {
			font-weight: 500;
		}
	}
	
	.comment-reply-title {
        margin: 0 0 37px;
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {
	        
            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
	            
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
}

.post-password-form input[type='password'] {
	display: block;
	max-width: 550px;
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;
		
		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include eltdfButtonDefaultStyle();
	@include eltdfButtonMediumParams();
	@include eltdfButtonSolidColor();
	cursor: pointer;
	
	&:hover {
		@include eltdfButtonSolidHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.eltdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.eltdf-slider-is-moving {
		
		.owl-item {
			
			.eltdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.eltdf-slider-has-both-nav {
        
        .owl-nav {
            
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: #fff;
			@include eltdfTransform(translateY(-50%) translateX(0));
			@include eltdfTransitionTransform(.3s $default-easing);
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 30px;
				line-height: 1;
				
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 30px;
			
			&:hover {
				@include eltdfTransform(translateY(-50%) translateX(-10px));
			}
		}
		
		.owl-next {
			right: 30px;
			
			&:hover {
				@include eltdfTransform(translateY(-50%) translateX(10px));
			}
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 8px 0 0;
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 15px;
				height: 15px;
				margin: 0 7px;
				border-radius: 3em;
				background-color: transparent;
				border: 2px solid #939393;
				box-sizing: border-box;
				@include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: #939393;
					border-color: #939393;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.eltdf-content {
			height: calc(100vh - 32px);
		}
	}

    .eltdf-content-inner {
	    @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
	    @include eltdfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .eltdf-404-title {
            margin: 0;
        }
	
	    .eltdf-404-subtitle {
            margin: 35px 0 0;
        }
	    
	    .eltdf-404-text {
		    margin: 10px 0 0;
	    }

        .eltdf-btn {
            margin: 30px 0 0;
	
	        &.eltdf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
	
	@include ipad-landscape {
		
		.eltdf-content {
			height: 100%;
			min-height: 100vh;
		}
		
		&.admin-bar {
			
			.eltdf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
	position: fixed;
	width: 55px;
	right: 25px;
	bottom: 25px;
	margin: 0;
	z-index: 10000;
	opacity: 0;
	visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(opacity 0.3s ease);
    }
	
	&:hover {
		> span {
			&.eltdf-btt-front {
				@include eltdfTransform(scale(1));
				@include eltdfTransitionTransform(.3s $default-easing .17s);
			}
			
			&.eltdf-btt-back {
				@include eltdfTransform(scale(0));
				@include eltdfTransitionTransform(.3s ease .17s);
			}
		}
	}

    > span {
	    position: relative;
	    display: block;
	    width: 55px;
	    height: 55px;
	    border-radius: 100%;
	    line-height: 53px;
	    font-size: 20px;
	    text-align: center;
	    color: #fff;
	    overflow: hidden;
	    z-index: 1;
	    @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);
	
	    &.eltdf-btt-front,
	    &.eltdf-btt-back {
		    position: absolute;
		    top: 0;
		    left: 0;
		    z-index: 0;
	    }
	
	    &.eltdf-btt-front {
		    background-color: $second-main-color;
		    border: 1px solid $second-main-color;
		    @include eltdfTransform(scale(0.5));
		    @include eltdfTransitionTransform(.3s ease .17s);
	    }
	
	    &.eltdf-btt-back {
		    background-color: $first-main-color;
		    border: 1px solid $first-main-color;
		    @include eltdfTransform(scale(1));
		    @include eltdfTransitionTransform(.3s ease .17s);
	    }
	    
	    > i {
		    position: relative;
		    left: 1px;
	    }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

